import { Component, OnInit} from '@angular/core';
import { FormGroup, FormBuilder, Validators, FormControl } from '@angular/forms';
import { Router } from '@angular/router';
import { AlertController } from '@ionic/angular';
import { environment } from 'src/environments/environment';
import { Authentication } from '../models/autentication';
import { AuthenticationService } from '../services/auhentication.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.page.html',
  styleUrls: ['./login.page.scss'],
})
export class LoginPage implements OnInit {

  ionicForm: FormGroup;
  pusername: string; 
  puserpassword : string;
  authenticated : boolean;
  
  authentication = {} as Authentication;
  aauthentication : Authentication[];

  autenticacao : any;

  constructor(public formBuilder: FormBuilder, private authenticationservice: AuthenticationService,
    public atrCtrl: AlertController, private router:Router) { }
  
  submitForm(){
    this.pusername = this.ionicForm.get('username').value;
    this.puserpassword = this.ionicForm.get('userpass').value;
    try{
      let uData = {
        companyLogin: this.pusername,
        companyPassword: this.puserpassword
      }
      this.authenticationservice.userAuthentication(uData);
    }catch(e){
      console.log('error'+e)
    }
  }

  async showInvalidCredentials(erro:any): Promise<any>{
    const alert = await this.atrCtrl.create({
      header:'ATENTION',
      subHeader:'Invalid Credentials',
      message: erro,
      buttons: ['OK']
    });
    alert.present();
  }

  ngOnInit() {
    this.ionicForm = new FormGroup({
      username: new FormControl(),
      userpass: new FormControl()
    });
    this.authenticated = false;
  }

}
